import React, { Component } from 'react';
import TextField from 'material-ui/TextField';
import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
import axios from 'axios';
import ImageResult from '../image-result/ImageResult';
import $ from 'jquery';




export default class Search extends Component {

    state = {
        searchText: '',
        amount: 15,
        apiUrl: 'https://pixabay.com/api',
        apiKey: '9304865-9863ff1b1e7d224364410fb6d',
        images: []

    };
    onTextChange = (event) => {
        const val = event.target.value;
        this.setState({[event.target.name]: event.target.value}, 
            () =>{

                if(val === ''){
                    this.setState({images: []});

                } else{

        //             axios.get(`${this.state.apiUrl}/?key=${this.state.apiKey}&q=${this.state.searchText}&image_type=photo&per_page=${this.state.amount}&safesearch=true`)
        // .then(res =>this.setState({images: res.data.hits}))
        // .catch(err => console.log(err));


        $.ajax({
          url:`https://pixabay.com/api/?key=${this.state.apiKey}&q=${this.state.searchText}&image_type=photo&per_page=${this.state.amount}&safesearch=true`,
          dataType:"json",
          cache: false,
          success: function(data){
            this.setState({images: data.hits}, function(){
              console.log(this.state);
              console.log(`https://pixabay.com/api/?key=${this.state.apiKey}&q=${this.state.searchText}&image_type=photo&per_page=${this.state.amount}&safesearch=true`);
              
            })
   
   
          }.bind(this),
          error: function(xhr,status,error){
            console.log(error);
          }
        });




                }


                
        
    }   );
    }

    onAmountChange = (event,index,value) => this.setState({amount: value});
  render() {
    //   console.log(this.state.images);
    return (
      <div>
          <TextField
          name="searchText"
          value={this.state.searchText}
          onChange={this.onTextChange}
          floatingLabelText="Search for images"
          fullWidth={true}
         />
         <br/>
         
          <SelectField
          name="amount"
          floatingLabelText="Amount"
          value={this.state.amount}
          onChange={this.onAmountChange}
        >
          <MenuItem value={5} primaryText="5" />
          <MenuItem value={10} primaryText="10" />
          <MenuItem value={15} primaryText="15" />
          <MenuItem value={30} primaryText="30" />
          <MenuItem value={50} primaryText="50" />
        </SelectField>

        
        <br/>

        {this.state.images.length >0 ? (<ImageResult images={this.state.images} />) : null }
      </div>
    );
  }
}




